<template>
  <div id="app" class="has-background-dark">
    <MobileHeader
      v-if="mode !== 'home' && !isMenuShowed"
      @toggleMenu="toggleMenu"
    />
    <SideNavbar
      :mode="mode"
      @changeMode="changePage"
      :show="isMenuShowed"
      @toggleMenu="toggleMenu"
    />
    <ButtomNavbar v-if="showButtomBar()" @changeMode="changePage" />
    <div :class="{ content: mode !== 'home' }">
      <component
        :is="mode"
        :mode="mode"
        :isMenuShowed="isMenuShowed"
        @toggleMenu="toggleMenu"
      />
    </div>
  </div>
</template>

<script>
import TitleSection from "./components/TitleSection.vue";
import ButtomNavbar from "./components/BottomNavbar";
import SideNavbar from "./components/SideNavbar";
import AboutPage from "./components/AboutPage";
import SkillPage from "./components/SkillPage";
import PortfolioPage from "./components/PortfolioPage";
import ContactPage from "./components/ContactPage";
import MobileHeader from "./components/MobileHeader";
import "bulma";

export default {
  name: "app",

  components: {
    ButtomNavbar,
    SideNavbar,
    MobileHeader,
    home: TitleSection,
    about: AboutPage,
    skills: SkillPage,
    portfolio: PortfolioPage,
    contact: ContactPage
  },

  data: () => {
    return {
      mode: "home",
      isMenuShowed: screen.width > 420
    };
  },

  methods: {
    changePage(page) {
      this.mode = page;
    },

    showButtomBar() {
      return this.mode === "home";
    },

    toggleMenu() {
      this.isMenuShowed = !this.isMenuShowed;
    }
  }
};
</script>

<style>
.content {
  padding: 1em 1em 0 0;
  min-height: 100vh;
  margin-left: 21%;
}

#app {
  font-family: "Book Antiqua", Helvetica, Arial, sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
  color: #2c3e50;
  height: auto;
}

.fade-leave-active {
  opacity: 1;
  transition: opacity 0.5s;
}

.fade-enter-active {
  opacity: 1;
  transition: opacity 1s;
  transition-delay: 0.5s;
}
.fade-enter,
.fade-leave-to {
  opacity: 0;
}

@media (max-width: 420px) {
  .content {
    margin-left: 0;
    padding: 1em;
    padding-top: 52px;
  }
}
</style>
