<template>
  <transition name="fade">
    <aside class="menu" v-if="renderMenu">
      <p class="menu-label">Menu</p>
      <ul class="menu-list">
        <li>
          <a @click="leave('home')">HOME</a>
        </li>
        <li>
          <a @click="leave('about')">ABOUT</a>
        </li>
        <li>
          <a @click="leave('skills')">SKILLS</a>
        </li>
        <li>
          <a @click="leave('portfolio')">PORTFOLIO</a>
        </li>
        <li>
          <a @click="leave('contact')">CONTACT</a>
        </li>
      </ul>
      <p class="menu-label">External link</p>
      <ul class="menu-list">
        <li>
          <a href="https://medium.com/@kusakawazeusu" target="_blank">MEDIUM</a>
        </li>
        <li>
          <a href="https://kuan-programmer.tw/" target="_blank">BLOG</a>
        </li>
      </ul>

      <transition name="slideDown" appear>
        <span
          class="icon is-small dismiss-btn"
          :class="{ 'icon-left': leaveAnimation }"
        >
          <font-awesome-icon
            size="3x"
            @click="leave('dismiss')"
            icon="chevron-circle-up"
            :style="{ color: 'white' }"
          />
        </span>
      </transition>
    </aside>
  </transition>
</template>

<script>
export default {
  name: "SideNavbar",
  props: ["mode", "show"],
  data: () => {
    return {
      leaveAnimation: false
    };
  },
  methods: {
    leave(destination) {
      if (destination === "dismiss") {
        this.leaveAnimation = true;
      } else {
        this.$emit("changeMode", destination);
      }

      setTimeout(() => {
        this.leaveAnimation = false;

        if (screen.width < 420) {
          this.$emit("toggleMenu");
        }
      }, 500);
    }
  },
  computed: {
    renderMenu() {
      if (screen.width > 420) {
        return this.mode !== "home";
      }

      return this.show;
    }
  }
};
</script>

