  display: none;
}

.fade-leave-active {
  opacity: 1;
  transition: opacity 0.5s;
}

.fade-enter-active {
  opacity: 1;
  transition: opacity 1s;
  transition-delay: 0.5s;
}
.fade-enter,
.fade-leave-to {
  opacity: 0;
}

.slideDown-enter-active {
  transition: all 0.5s;
  transition-delay: 0.75s;
  opacity: 1;
}

.slideDown-enter {
  transform: translateY(-50px);
  opacity: 0;
}

@media (max-width: 420px) {
  aside {
    width: 100%;
    z-index: 1;
  }

  .dismiss-btn {
    display: block;
    margin: 0 auto;
    border-radius: 50%;
  }

  .dismiss-btn * {
    transform: translateX(-25%);
  }

  .icon-left {
    animation: 0.5s ease-in-out slide-up forwards;
  }

  @keyframes slide-up {
    to {
      transform: translateY(-60px);
      opacity: 0;
    }
  }
}
</style>
